import { useState, useEffect } from "react";
import Note from "./components/Note";
import React from 'react';
import noteServices from './sevices/notes'
import Notification from "./components/error";
import Footer from "./components/Footer";

const App = () => {
  const [notes, setNotes] = useState([])
  const [newNote, setNewNote] = useState('')
  const [showAll, setShowAll] = useState(false)
  const [errorMessage, setErrorMessage] = useState(null)

  useEffect(() => {
    noteServices.getAll()
      .then(response => {
        setNotes(response)
      })
  }, [])

  const addNote = (event) => {
    event.preventDefault()
    const noteObject = {
      content: newNote,
      important: Math.random() < 0.5,
      id: notes.length + 1,
    }
  
    noteServices.create(noteObject)
    .then(response => {
      setNotes(notes.concat(response));
      setNewNote('');
    })
  }
  const toggleImportanceOf = (id) => {
    const note = notes.find(note => note.id === id)
    const changedNote = {...note, important: !note.important}
    noteServices.update(id, changedNote)
    .then(response => {
      setNotes(notes.map(note => note.id !== id ? note : response))
    })
    .catch(error => {
      setErrorMessage(
        `Note '${note.content}' was already removed from server`
      )
      setTimeout(() => {
        setErrorMessage(null)
      }, 5000)
      setNotes(notes.filter(n => n.id !== id))
    })
  }
  const handleChange = (event) => {
    setNewNote(event.target.value)
  }

  const notesToShow = showAll 
    ? notes 
    : notes.filter(note => note.important === true);

  return (
    <div>
      <h1>Notes</h1>
      <Notification message={errorMessage} />
      <div>
        <button onClick={() => setShowAll(!showAll)}>
          show {showAll ? "impotant" : 'all'}
        </button>
      </div>
      <ul>
        {notesToShow.map(ele => 
          <Note key={ele.id} note={ele} toggleImportance = {() => toggleImportanceOf(ele.id)}></Note>
        )}
      </ul>
      <form onSubmit={addNote}>
        <input onChange={handleChange} value={newNote}/>
        <button type="submit">save</button>
      </form>  
      <Footer></Footer>
    </div>
  )
}

export default App